*{
  font-family: Arial, Helvetica, sans-serif;   
}

.cont{
  display: grid;
  align-items: center;
}


.navbar{
  display: flex;
  justify-content: space-evenly;
  background-color: rgb(10, 9, 9);
  color: aliceblue;
  position: fixed;
  width: 100vw;
  height: 10vh;
  margin-top: -0rem;
  margin-left: -1rem;
  font-size: larger;
}
.nav-item{
  list-style: none; 
  /* font-size:larger; */
}


.nav-menu{
  width: 50vw;
  display: grid;
  grid-template-columns: repeat(6,1fr);
  justify-content: space-evenly;  
}

.nav-menu-active{
  display: none;
}
.hamber{
  display: none;
}
#resume-in-navbar{
  background-color:transparent;
  border: none;
  color: aliceblue;
  font-size: medium;
  margin: auto;
  font-size: large;
}

@media screen and (max-width:800px) {
.nav-menu{
  display: none;
}   
.hamber{
  display: block;
  margin-top: 1.5rem;    
}

.nav-menu-active{
  position: absolute;
  display: grid;
  background-color: rgb(45,45,45);
  right: 0;
  top: 2.7rem;
  transition: all 0.5s ease-out;
  width: 50vw;
  height: 50vh;
  z-index: 1;

}
.nav-item{
  margin-left: 15vw;
  margin-top: 1rem;
  
} 
#resume-in-navbar{
  margin-left: -1vw;
}  
}









